import React from 'react';
import Router from 'next/router';

const redirect = (context, target) => {
  if (context.res) {
    context.res.writeHead(303, { location: target });
    context.res.end();
  } else {
    Router.replace(target);
  }
};



export default (c) => {
  return <div>Hello</div>
};
